.openingContainer h6 {
    text-align: start;
}

.openingContainer small {
    font-size: 0.8rem;
    margin-block-end: 0.5rem;
    color: #777;
}

.jobDrawer .css-1160xiw-MuiPaper-root-MuiDrawer-paper {
    top: 64px !important;
    max-width: 50% !important;
}

.openingInfos {
    text-align: center;
}
.overlay {
    background: #0000008a;
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    display: none;
}
.jobDrawer {
    position: absolute;
    top: 64px;
    right: 0;
    bottom: 0;
    max-width: 50%;
    background-color: white;
    transition: all 0.2s ease;
    transition-delay: 0s;
    transform: translateX(99999px);
}
.showDrawer{
    transform: translateX(0px);
}
@media (max-width: 768px) {
    .openingContainer {
        grid-template-columns: none !important;
        grid-gap: 1rem !important;
        justify-items: center;
    }
    .openingContainer h6 {
        text-align: center !important;
    }
    .details {
        grid-template-columns: repeat(3, 1fr) !important;
        grid-gap: 1rem !important;
        justify-items: start;
    }
}

@media (max-width: 500px) {
    .details {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
    }
}
